@font-face {
    font-family: 'Inter';
    src: url(./Inter-Regular.woff2) format('woff2'),
    url(./Inter-Regular.woff) format('woff');
    font-style: normal;
    font-weight: 400;
    font-display: swap;
}

@font-face {
    font-family: 'Inter';
    src: url(./Inter-Bold.woff2) format('woff2'),
    url(./Inter-Bold.woff) format('woff');
    font-style: normal;
    font-weight: 700;
    font-display: swap;
}

@font-face {
    font-family: 'Inter';
    src: url(./Inter-Black.woff2) format('woff2'),
    url(./Inter-Black.woff) format('woff');
    font-style: normal;
    font-weight: 900;
    font-display: swap;
}